}

body {
  background: #e5e5e5;
  height: 100%;
}

header, footer {
  width: 100%;
  flex: 0 0 auto;
}

footer {
  background: #fff;
}

main {
  flex: 1 0 auto;
}

.container {
  max-width: 1920px;
  margin: 0 auto;
  height: 100%;
}

.general {
  background: url(../img/background.webp) no-repeat right top;
}

.round {
  background: url('../img/background.webp') no-repeat right top,
              url('../img/round-firstscreen.webp') no-repeat right top;
}

.header, .footer {
