  max-height: 184px;
  padding: 70px 100px;
}

.header--logo {
  width: 300px;
}

.header--main-menu ul{
  column-gap: 80px;
}
.header--contacts {
  column-gap: 45px;
}

.main {
  display: flex;
  margin: 0 auto;
  width: 100%;
  flex-direction: column;

  .first, .second {
    align-self: center;
  }
}

.container-inner {
  margin: 0 auto;
  width: 100%;
}

.social-block {
  padding-bottom:80px;
}

.social-icons {
  column-gap: 40px;
}

.social-icon-container {
  width: 76px;
  height: 76px;
  border-radius: 50px;
