}

.social-icon-container-default {
  background: #EE964B;
  box-shadow: -10px -10px 30px rgba(255, 255, 255, 0.25), 10px 10px 30px rgba(0, 0, 0, 0.1), inset -10px -10px 30px #FF9F4D, inset 10px 10px 30px rgba(0, 0, 0, 0.1);
}

.social-icon {
  width: 30px;
  height: 30px;
  margin: 23px;
  background: no-repeat center center;
}

.icon__whatsapp {
  background: url(../img/whatsapp.svg);
}

.icon__instagram {
  background: url(../img/instagram.svg);
}

.--rounded {
  width: 100%;
  margin: 0 170px;
  padding: 80px 150px;
  background: #F9F9F9;
  box-shadow: -20px -20px 50px #FFFFFF, 20px 20px 50px rgba(0, 0, 0, 0.05), inset -20px -20px 50px #FFFFFF, inset 20px 20px 50px rgba(0, 0, 0, 0.05);
  border-radius: 322.5px;
}

.index__first-screen {
  .first-screen__title {
    padding-top: 160px;
