      text-align: center;
      font-size: 165px;
      line-height: 202px;
      letter-spacing: 0.02em;
      text-shadow: 0px 20px 50px rgba(0, 0, 0, 0.2);
      color: #fff;
    }
    h6 {
      text-align: center;
      line-height: 37px;
      color: rgba(22, 22, 22, 0.8);
    }
  }
  .first-screen__benefits {
