    column-gap: 25px;
    .single-benefit {
      background: #FFFFFF;
      box-shadow: -20px -20px 50px #FFFFFF, 20px 20px 50px rgba(0, 0, 0, 0.05), inset -20px -20px 50px #FFFFFF, inset 20px 20px 50px rgba(0, 0, 0, 0.05);
      border-radius: 300px;
      font-size: 23px;
      line-height: 28px;
      text-align: center;
    }
    .single-benefit-1 {
      width: 298px;
      height: 298px;
    }
    .single-benefit-2 {
      width: 269px;
      height: 269px;
      margin-top: 148px;
      margin-bottom: 81px;
    }
    .single-benefit-3 {
      width: 394px;
      height: 394px;
      margin-top: 23px;
      margin-bottom: 81px;
    }
    .single-benefit-4 {
      width: 315px;
      height: 315px;
      align-self: flex-end;
    }
    .single-benefit-5 {
      width: 239px;
      height: 239px;
      margin-top: 68px;
      margin-bottom: 191px;
    }
  }
}

.single-benefit-inner {
  border: 1px solid #EE964B;
  box-sizing: border-box;
  margin: 17px;
  border-radius: 300px;
  width: calc(100% - 34px);
  height: calc(100% - 34px);
  p {
    padding: 25px;
  }
}

.single-benefit-inner:hover {
  border: 1px solid rgba(22, 22, 22, 0.8);
}

.support {
  background: url(../img/support-background.webp) top center no-repeat;
  padding-bottom: 240px;
}

.support-heading {
  text-align: center;
  padding-top: 140px;
}

.support-cards {
  flex-wrap: wrap;
  margin: 80px 150px 0;
  column-gap: 80px;
}

.support-card {
  background: #FFFFFF;
  box-shadow: -10px -10px 50px #D8D8D8, 10px 10px 50px rgba(0, 0, 0, 0.09);
  border-radius: 30px;
  margin-bottom: 80px;
  min-width: 308px;
  min-height: 168px;
  flex: 1 0 auto;
  p {
    padding: 25px 40px 70px;
  }
}

.support-card-icon {
  width: 25px;
  height: 25px;
  position: relative;
  top: 25px;
  left: calc(100% - 50px);
  background: #EE964B;
  border-radius: 25px;
  cursor: pointer;
}

.support-card-icon::after {
  content: '?';
  color: #fff;
  position: relative;
  top: 4px;
  left: 8px;
}

.support-card-icon:hover {
  background: rgba(22, 22, 22, 0.8);
}

.situation {
  background: #EE964B;
  box-shadow: -10px -10px 50px #BDBDBD, 10px 10px 50px rgba(0, 0, 0, 0.14);
  border-radius: 30px;
  margin: -220px 100px 166px 100px;
  width: calc(100% - 200px);
  padding: 16px 20px;
}

.situation-callback {
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  filter: drop-shadow(-10px -10px 50px rgba(0, 0, 0, 0.25)) drop-shadow(10px 10px 50px rgba(99, 45, 0, 0.28));
  border-radius: 30px;
  padding-top: 110px;
  padding-bottom: 90px;
  h5 {
    margin-bottom: 25px;
  }
  p {
    margin-bottom: 45px;
  }
  span {
    color: #fff;
  }
}

.anketa {
  background: #F9F9F9;
  box-shadow: -20px -20px 50px #FFFFFF, 20px 20px 50px rgba(0, 0, 0, 0.05), inset -20px -20px 50px #FFFFFF, inset 20px 20px 50px rgba(0, 0, 0, 0.05);
  border-radius: 322.5px;
  margin: 0 100px 135px;
  width: calc(100% - 200px);
}

.anketa-image {
  border-radius: 360px;
  max-width: 645px;
}

.anketa-description {
  padding: 110px 0 110px 60px;
}

.anketa-description-subheading {
  margin-left: 100px;
  margin-bottom: 5px;
  h6 {
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.025em;
    color: #EE964B;
  }
}

.anketa-description-subheading::before {
  content: "";
  width: 50px;
  height: 2px;
  background: #ee964b;
  display: block;
  position: relative;
  left: -100px;
  top: 11px;
}

.anketa-description-heading {
  margin-bottom: 44px;
}

.anketa-description-content {
  margin-bottom: 50px;
  ul {
    padding-left: 0;
  }
  li {
    margin-left: 20px;
    margin-bottom: 30px;
  }
  li:last-of-type {
    margin-bottom: 0;
  }
  li:before {
    content: '+';
    position: relative;
    left: -20px;
  }
}

.slider-phone {
  column-gap: 235px;
  padding-top: 128px;
}

.slider-phone-left {
  h5 {
    line-height: 49px;
    letter-spacing: 0.025em;
    margin-bottom: 30px;
  }
  p {
    line-height: 25px;
    letter-spacing: 0.025em;
    margin-bottom: 50px;
  }
}
.slider-phone-arrows {
  padding: 0 50px;
}

.slider-phone-arrows div {
  width: 70px;
  height: 70px;
  cursor: pointer;
  border-radius: 100px;
  margin-right: 50px;
}

.left-arrow {
  background: url(../img/left.svg) no-repeat center center, rgba(22, 22, 22, 0.8);
}

.right-arrow {
  background: url(../img/right.svg) no-repeat center center, rgba(22, 22, 22, 0.8);
}

.left-arrow:hover, .left-arrow:active {
  background: url(../img/left.svg) no-repeat center center, #EE964B;
}

.right-arrow:hover, .right-arrow:active {
  background: url(../img/right.svg) no-repeat center center, #EE964B;
}

.mask {
  background: url(../img/phone.webp);
  width: 383px;
  height: 774px;
}

.mask-content>img {
  padding: 20px;
  border-radius: 60px;
}

.slider-text {
  line-height: 98px;
  letter-spacing: .025em;
  transform: translateY(-100%) translateX(-1%) rotate(90deg);
  transform-origin: left bottom;
  max-height: 100px;
  max-width: 98px;
  display: flex;
  flex-flow: column;
}

.after-consultation {
  margin-top: 350px;
}

.after-consultation-inner {
  background: #EE964B;
  box-shadow: 20px 20px 50px rgba(166, 84, 15, 0.29), inset -10px -10px 50px #FF8F2F, inset 20px 20px 50px rgba(154, 73, 4, 0.53);
  border-top-right-radius: 307px;
  border-bottom-right-radius: 307px;
  margin-right: 345px;
  padding: 115px 360px 115px 255px;
  h5 {
    line-height: 160%;
    letter-spacing: 0.025em;
    padding-bottom: 30px;
  }
  span {
    color: #fff;
  }
  p {
    font-size: 20px;
    line-height: 180%;
    padding-bottom: 20px;
  }
}

.programs {
  margin: 250px 0 250px 100px;
  width: calc(100% - 100px);
}

.col-50 {
  width: 50%;
}

.programs-left {
  h2 {
    padding-bottom: 20px;
  }
  p {
    padding-bottom: 60px;
  }
  .slider-phone-arrows {
    padding: 0;
  }
}

.programs-right {
  column-gap: 50px;
}

.program-card {
  width: 438px;
  height: 671px;
  border-radius: 30px;
  background: url(../img/card-background.svg) center center;
  .container-inner {
    border: 1px solid #EE964B;
    box-sizing: border-box;
    filter: drop-shadow(-10px -10px 50px rgba(0, 0, 0, 0.25)) drop-shadow(10px 10px 50px rgba(99, 45, 0, 0.28));
    border-radius: 30px;
    margin: 7px 20px 20px;
    width: calc(100% - 40px);
    min-height: calc(100% - 27px);
    padding: 64px 0;
    align-content: space-between;
  }
}

.card-name {
  text-align: center;
  min-height: 100px;
}

.card-description {
  line-height: 160%;
  text-align: center;
  letter-spacing: 0.025em;
  color: rgba(22, 22, 22, 0.8);
  padding: 0 33px;
}

.individual {
  margin: 185px 155px 155px;
  width: calc(100% - 310px);
  h2 {
    padding: 80px 40px 30px;
    white-space: nowrap;
    text-align: center;
  }
  p {
    padding-bottom: 80px;
    text-align: center;
  }
}

.individual-bg {
  background: url(../img/individual-bg.webp) no-repeat center center;
}

.roadmap {
  background: url(../img/roadmap-bg.webp) no-repeat top center;
  background-size: cover;
  padding: 145px 170px 170px 90px;
  h4 {
    line-height: 74px;
    text-align: center;
    letter-spacing: 0.025em;
    padding-bottom: 71px;
  }
  h5 {
    line-height: 160%;
    text-align: center;
    letter-spacing: 0.025em;
    color: #444444;
    padding: 0 145px 100px;
  }
}

.roadmap-img {
  padding-bottom: 90px;
  width: 100%;
}

.form {
  padding: 195px 110px;
  padding-top: calc(195px - 55px);
  .form-text {
    align-self: baseline;
    h5{
      line-height: 49px;
      letter-spacing: 0.02em;
      color: #444444;
      padding-bottom: 17px;
      //padding-top: 55px;
      //background: url(../img/form-bg.png) no-repeat center;
      background-position: -100px;
    }
    p {
      padding-bottom: 25px;
    }
  }
  .form-input {
    align-self: flex-end;
  }
  form {
    width: 845px;
    height: 127px;
    background: #EE964B;
    box-shadow: 20px 20px 50px rgba(199, 105, 26, 0.08), inset -10px -10px 50px #FF9032, inset 20px 20px 50px #C7691A;
    border-radius: 307px;
    display: flex;
    align-content: center;
    input {
      background: rgba(0,0,0,0);
      border: none;
      padding: 50px;
      font-family: CenturyGothic;
      font-size: 20px;
      line-height: 25px;
      text-align: center;
      color: #CF670E;
    }
    input::placeholder {
      color: #CF670E;
      text-align: center;
    }
  }
}

#text {
  padding-left: 66px;
  flex-shrink: 10;
  flex-grow: 0;
  width: 218px;
}

#tel {
  flex-shrink: 10;
  flex-grow: 0;
  width: 263px;
}

#email {
  flex-shrink: 10;
  flex-grow: 0;
  width: 237px;
}

#submit {
  background: url(../img/input.svg) center center no-repeat;
  box-shadow: 10px 10px 50px rgba(0, 0, 0, 0.1), inset -10px -10px 30px #FFFFFF, inset 10px 10px 30px rgba(0, 0, 0, 0.18);
  border-radius: 307px;
  width: 127px;
  height: 127px;
  background-color: #F9F9F9;
  cursor: pointer;
}

.subfooter {
  background: url(../img/subfooter-bg.webp) no-repeat left;
}

.our-team {
  padding: 0 115px 230px 90px;
  column-gap: 167px;
}

.our-team__portrait {
  width: 22%;
  .portrait-name {
    font-size: 30px;
    line-height: 37px;
    letter-spacing: 0.02em;
    color: #444444;
    padding-bottom: 20px;
    padding-left: 50px;
  }
  .portrait-description {
    font-size: 20px;
    line-height: 25px;
    color: #444444;
    padding-left: 50px;
  }
}

.our-team__text {
  width: 75%;
  align-self: flex-end;
  h5 {
    font-size: 40px;
    line-height: 64px;
    letter-spacing: 0.025em;
    color: rgba(0, 0, 0, 0.8);
    padding-bottom: 60px;
  }
}

.our-team__buttons {
  column-gap: 40px;
  padding-bottom: 20px;
}

.testimonials {
  margin-bottom: 90px;
}

.testimonials-grid {
  margin-top: 70px;
  flex-wrap: wrap;
  padding: 0 100px;
}

.testimonial {
  max-width: 394px;
  max-height: 415px;
  min-width: 394px;
  min-height: 415px;
  background: #F9F9F9;
  box-shadow: -10px -10px 50px #FFFFFF, 20px 20px 50px rgba(0, 0, 0, 0.08), inset -10px -10px 50px #FFFFFF, inset 20px 20px 50px rgba(0, 0, 0, 0.1);
  border-radius: 40px;
}

.testimonial-text {
  padding: 50px;
  display: flex;
  flex-direction: column;
  
  p {
    flex: 1 0 auto;
    font-size: 20px;
    line-height: 25px;
    color: #444444;
  }
  .testimonial-name {
    font-size: 23px;
    line-height: 28px;
    color: #EE964B;
  }
  .testimonial-state {
    flex: 1 0 auto;
    font-size: 16px;
    line-height: 20px;
    color: #444444;
  }
}

.testimonial:nth-child(even) {
  margin-top: 50px;
}

@media screen and (max-width: 1919px) {
  h1 {
    font-size: 145px;
  }
  
  h2 {
    font-size: 80px;
    line-height: 123px;
  }
  
  h3 {
    font-size: 60px;
  }
  
  h4 {
    font-size: 40px;
  }
  
  h5 {
    font-size: 30px;
  }
  
  h6 {
    font-size: 20px;
  }
  
  p {
    font-size: 18px;
    color: rgba(22, 22, 22, 0.8);
  }
  
  a {
    font-size: 16px;
  }

  .general {
    max-width: 100%;
  }

  .container {
    max-width: 100%;
  }
  .header--main-menu ul {
    column-gap: 40px;
  }
  .header--logo {
    width: 250px;
  }

  .header--contacts {
    flex-direction: column;
    row-gap: 20px;
  }
  .main {
    max-width: 100%;
  }
  .index__first-screen {
    .first-screen__benefits {
      margin: 220px 80px 90px;
      column-gap: 0;
      .single-benefit-1, .single-benefit-2 {
        width: 250px;
        height: 250px;
      }
      .single-benefit-2 {
        margin-bottom: 30px;
      }
      .single-benefit-3 {
        width: 355px;
        height: 355px;
        align-self: flex-start;
        margin-top: 0;
      }
      .single-benefit-4 {
        width: 250px;
        height: 250px;
      }
      .single-benefit-5 {
        width: 170px;
        height: 170px;
        margin-top: 120px;
        margin-bottom: 100px;
      }
    }
  }
  .support {
    .support-cards {
      .support-card {
        min-width: 250px;
        margin-bottom: 60px;
      }
    }
  }

  .anketa {
    margin: 0 100px;
    object {
      max-width: 450px;
    }
    .anketa-description {
      padding: 60px 0 60px 80px;
      justify-self: center;
      .anketa-description-subheading {
        margin-left: 70px;
      }
      .anketa-description-subheading::before {
        left: -70px;
      }
      .anketa-description-heading, .anketa-description-content {
        margin-bottom: 25px;
      }
      .anketa-description-content {
        ul {
          li {
            margin-bottom: 15px;
          }
        }
      }
    }
  }
  .slider-phone {
    column-gap: 150px;
  }
  .after-consultation {
    margin-top: 150px;
  }
  .after-consultation-inner {
    margin-right: 245px;
    padding: 80px 160px 90px 150px;
  }
  .programs {
    margin: 150px 0 100px 100px;
    width: calc(100% - 100px);
    justify-content: space-around;
    .programs-left.col-50 {
      width: 40%;
    }
    .programs-right {
      column-gap: 40px;
      width: 60%;
      .program-card {
        max-height: 550px;
        width: 100%;
        max-width: 430px;
      }
    }
  }
  .individual {
    margin: 165px 100px 100px;
    width: calc(100% - 200px);
    .individual-bg {
      background-size: contain;
    }
  }
  .roadmap {
    padding: 120px 150px 150px 150px;
    .roadmap-img {
      width: 100%;
      object {
        max-width: 100%;
      }
    }
  }
  .form {
    padding: 120px 80px 150px;
    margin: 0 auto;
    justify-content: start;
    align-items: center;
    .form-text.col-50 {
      align-self: center;
      flex-shrink: 1;
    }
    .form-input.col-50 {
      width: 50%;
      align-self: center;
      flex-shrink: 0;
    }
    form {
      width: 100%;
      height: 115px;
      justify-content: space-between;
      input {
        padding: 25px 5px;
      }
      input:focus {
        border: 0;
      }
    }
  }
  #text {
    padding-left: 36px;
  }
  #tel, #text, #email {
    width: calc((100% - 115px) / 3);
    flex-shrink: 10;
  }
  #submit {
    height: 115px;
    width: 115px;
    justify-self: flex-end;
    flex-shrink: 0;
  }
  .our-team {
    column-gap: 0;
    .our-team__portrait {
      img {
        width: 100%;
      }
      .portrait-name, .portrait-description {
        padding-left: 35px;
      }
    }
    .our-team__text {
      width: 66%;
      h5 {
        font-size: 33px;
        line-height: 52px;
        padding-bottom: 90px;
      }
    }
  }
  .testimonials {
    .testimonials-grid {
      display: grid;
      justify-items: center;
      justify-content: center;
      grid-template-rows: auto;
      grid-template-columns: repeat(3, minmax(100px, 4fr));
      align-content: center;
      align-items: center;
      column-gap: 20px;
      row-gap: 20px;
      .testimonial {
        max-width: calc((100% - 200px) / 3);
        width: auto;
        //min-height: 366px;
        margin: 10px 0;
      }
    }
  }
  .--rounded {
    padding: 80px;
    margin: 0 85px;
  }
}

